import axios from 'axios';
import qs from 'qs';
import router from './router'
import Vue from 'vue';//vue
import { reduce } from 'xe-utils/methods';
let v = new Vue();
axios.defaults.timeout = 200000;//
axios.defaults.headers.post['Content-Type'] = "application/json";
axios.defaults.baseUrl = 'http://192.168.3.58:9990/manxi-reinforce';//
//,localStoragetoken,,,
//token,(token)05,05,token
//,token
axios.interceptors.request.use(config => {
    if (localStorage.getItem('Authorization')) {
        config.headers.Authorization = localStorage.getItem('Authorization')
    }
    return config;
}, error => {
    return Promise.reject(error)
})
axios.interceptors.response.use(response => {
    if (response.data.code === "05") {
        localStorage.removeItem('Authorization');
        v.$alert(',', '', {
            confirmButtonText: '',
            type: "warning",
            callback: action => {
                router.push({ name: 'Login' });
                location.reload(true);
            }
        })
    }
    if (response.data.code === '500' || response.data.code === '01' || response.data.code === '99') {
        v.$notify({
            title: "",
            message: response.data.message,
            type: "warning",
            duration: 3000
        });
    }
    return response;
}, error => {
    v.$alert('', '', {
        confirmButtonText: '',
        type: "warning",
        callback: action => {
            router.push({ name: 'Login' });
            location.reload(true);
        }
    })
})
//Promise(post)
export function fetchPost(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(url, params).then(res => {
            resolve(res);
        }, err => {
            reject(err);

        }).catch((error) => {
            reject(error)
        })
    })
}
//Promise(get)
export function fetchGet(url, params, responseType) {
    return new Promise((resolve, reject) => {
        axios.get(url, { params }, responseType).then(res => {
            resolve(res);
        }, err => {
            reject(err)
        }).catch((error) => {
            reject(error)
        })
    })
}

//
export function uploadFile(url, param, config) {
    let axiosConfig = {
        url: url,
        method: 'post',
        data: param,
    }
    if (config instanceof Object) {
        for (let key in config) {
            axiosConfig[key] = config[key]
        }
    }
    return axios(axiosConfig)
}
export default {
    fetchGet, fetchPost, uploadFile
}


